<template>
   <div>
      <div v-if="!myData">
         <content-loader
            :animate="true"
            :height="300"
            :width="550"
            :speed="2"
            primaryColor="#f3f3f3"
            secondaryColor="#ecebeb"
            style="margin-top:50px;"
         >
            <rect x="60" y="0" rx="0" ry="0" width="150" height="20" />
            <rect x="400" y="0" rx="0" ry="0" width="70" height="20" />

            <rect x="60" y="40" rx="0" ry="0" width="410" height="35" />
            <rect x="60" y="80" rx="0" ry="0" width="410" height="35" />
            <rect x="60" y="120" rx="0" ry="0" width="410" height="35" />
            <rect x="60" y="160" rx="0" ry="0" width="410" height="35" />


         </content-loader>
      </div>
      <div class="BxHNotif" v-if="myData">
         <div class="BxHNotif__BxHeader">
            <div class="BxHNotif__BxHeader--titles">
               Notifications
            </div>
            <div class="BxHNotif__BxHeader--btn">
               <v-btn class="btnRound liveBtn" @click="deleteall" :disabled="this.CheckLength">
                  Clear All  
               </v-btn>
            </div>
         </div>
         <div class="BxHNotif__BxContent">
            <div v-if="!this.CheckLength">
               <div class="BxHNotif__BxContent--NotifAndTime" v-for="(n, index) in FilterGetNotifAll" :key="index">
                  <div class="BxAvatar">
                     <!-- <v-avatar size="50" color="grey lighten-4">
                        <img src='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSaaaCAB1h54hr1tn437LGB5HVLyMug3ZndH9SndFxPauIKl3pW' alt="avatar">
                     </v-avatar> -->
                  </div>
                  <div class="BxDecAndTime">
                     <div class="BxDecAndTime__Desc">
                        {{n.description}}
                     </div>
                     <div class="BxDecAndTime__Time">
                        {{n.created_at | dateFormat}}
                     </div>
                  </div>
               </div>
            </div>
            <div v-if="this.CheckLength">
            <div class="BxHNotif__BxContent--NotifAndTime">
                  <div class="BxDecAndTime">
                     <div class="BxDecAndTime__Desc" style="text-align:center;width:100%;">
                        You don't have notifications yet
                     </div>
                  </div>
               </div>
            </div>
         </div>
         <v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
            <v-card>
               <v-card-text class="text-xs-center">
                  <v-progress-circular
                     indeterminate
                     color="primary"
                     :size="100"
                     :width="15"
                  ></v-progress-circular>
               </v-card-text>
            </v-card>
         </v-dialog>
      </div>
   </div>
</template>

<script>
   import moment from 'moment'
   import { ContentLoader } from "vue-content-loader"
   export default {
      data(){
         return{
            notifications: [],
            snackbar: false,
            timeout: 6000,
            NotifColor:'',
            NotifText:'',
            dialogLoding: false,
            CheckLength: false,
            myData :null,
         }
      },
      components:{
         ContentLoader
      },
      created() {
         this.$store.dispatch("GET_NOTIFICATIONS_ALL");
      },
      mounted () {
         this.$nextTick( () => {
            setTimeout(() => {
               this.CheckLoad
            }, 2000);
         })
      },
      methods: {
         deleteall(){
           this.dialogLoding = true
         }
      },
      computed:{
         GET_NotifAll(){
            return this.$store.state.HeaderNotifALL
         },

         FilterGetNotifAll(){
            const DataNotif = this.GET_NotifAll.data
            const items = []

            for (var n in DataNotif){
               moment(DataNotif[n].created_at).format('MMMM, Do YYYY HH:mm:ss')
               items.push(DataNotif[n])
            }

            function compare(a, b){
               if (a.created_at > b.created_at) return -1;
               if (a.created_at < b.created_at) return 1;
            }
            return [...DataNotif].sort(compare)

         },

         CheckLoad(){
            if (this.$store.getters.CCK_NOTIF_ALL !== 'IS NULL'){
               const ItemsCont = []
               for (var i in this.$store.getters.CCK_NOTIF_ALL){
                  ItemsCont.push(this.$store.getters.CCK_NOTIF_ALL[i])
               }
               return ItemsCont
            }else{
               return 'IS NULL'
            }
         },

         CheckLoadDelete(){
            if (this.$store.getters.CCK_DELETE_NOTIL_ALL !== 'IS NULL'){
               const ItemsCont = []
               for (var i in this.$store.getters.CCK_DELETE_NOTIL_ALL){
                  ItemsCont.push(this.$store.getters.CCK_DELETE_NOTIL_ALL[i])
               }
               return ItemsCont
            }else{
               return 'IS NULL'
            }
         }
      },
      filters:{
         dateFormat: function (value) {
            if (value) {
               return moment(String(value)).format('ddd, D MMMM YYYY HH:mm:ss')
            }
         },
      },
      watch: {
         dialogLoding(val){
            if(!val) return
            this.$store.dispatch("DELETE_ALL_NOTIFICATIONS");
         },
         GET_NotifAll(val){
            if(val.data.length == 0){
               this.CheckLength = true
               //console.log(val)
            }else{
               this.CheckLength = false
            }
         },
         CheckLoadDelete(val){
            const DataDeleteAll = val[0]

               if (DataDeleteAll == 200 ){
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'no',
                     TextAlert : 'Success'
                  })
                  
               }else if(DataDeleteAll == 400){
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'yes',
                     TextAlert : 'Bad Request, Please try again in a few minutes'
                  })
               }else if(DataDeleteAll == 500){
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'yes',
                     TextAlert : 'Server Internal Error, Please try again in a few minutes'
                  })
               }
               setTimeout(() => {
                  this.$store.dispatch("GET_NOTIFICATIONS_LATEST")
                  this.dialogLoding = false
               }, 1000); 
         },
         CheckLoad(val){
            
            if (val === 'IS NULL'){
               this.$store.dispatch("GET_NOTIFICATIONS_ALL");

               setTimeout(() => {
                  if (val === 'IS NULL'){
                     this.$store.commit('MuttAllertCont', {
                        Alert : 'yes',
                        TextAlert : 'Something wrong, Please Reload your Browser'
                     })
                     this.myData = null
                     console.log(val)
                  }
               }, 1000);
            
            }else{
               const DataNotifAll = val[0]

               if (DataNotifAll == 200 ){
                  this.myData = 'Success'
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'no',
                     TextAlert : 'Success'
                  })
               }else if(DataNotifAll == 400){
                  this.myData = null
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'yes',
                     TextAlert : 'Bad Request, Please try again in a few minutes'
                  })
               }else if(DataNotifAll == 500){
                  this.myData = null
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'yes',
                     TextAlert : 'Server Internal Error, Please try again in a few minutes'
                  })
               }
            }
         }
         
      },
      destroyed() {
         this.$store.commit('MuttAllertCont', {
            Alert : 'no',
            TextAlert : 'exit'
         })
      },
   }
</script>


<style lang="scss">
   @import './HeaderNotifications.scss';
</style>




